body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	margin: 0;
}

.App{
	background-color: rgb(24, 24, 24);
	color: white;
}
.modal {
	/* border: 2px solid white; */
	position: fixed;
	display: block;
	/* width: 100%;
  height: 100%; */
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.7);
	z-index: 2;
}

.modal-content {
	/* border: 2px solid red; */
	position: absolute;
	top: 375px;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	width: 60%;
}

.modal-header {
	/* border: 2px solid yellow; */
	height: 30px;
}

.modal-button {
	position: absolute;
	z-index: 1;
	top: 20px;
	right: 20px;
	height: 25px;
	width: 25px;
	border-radius: 50%;
	padding: 5px;
	/* border: none; */
	background-color: #181818;
	cursor: pointer;
}

.modal-button-1 {
	position: absolute;
	z-index: 1;
	bottom: 30px;
	left: 30px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: white;
	color: black;
	width: 120px;
	height: 40px;
	border-radius: 5px;
	cursor: pointer;
	/* padding: 2px; */
}

.modal-button-1-wrapper {
	font-weight: 600;
}

.modal-button-1:hover {
	background-color: #e6e6e6;
}

.modal-button-1 > button {
	background-color: transparent;
	border: none;
}

.player-wrapper {
	position: relative;
	padding-top: 56.25%; /* Player ratio: 100 / (1280 / 720) */
}

.react-player {
	position: absolute;
	top: 0;
	left: 0;
}

.modal-body {
	/* border: 2px solid yellow; */
	background-color: rgb(24, 24, 24);
	display: flex;
	justify-content: space-between;
	padding: 25px;
}

.modal-body-container {
	width: 75%;
}

.modal-info-1 {
	padding-top: 10px;
	width: 90%;
}

.modal-info {
	display: flex;
	align-items: center;
}

.modal-info > p {
	padding: 5px;
}

.modal-info-0 {
	color: rgb(146, 216, 41);
	font-weight: 600;
}

.modal-info > div {
	border: 1px solid white;
	border-radius: 5px;
	background-color: rgba(0, 0, 0, 0.3);
	width: 35px;
	text-align: center;
}

.modal-body-info {
	width: 25%;
}

.modal-body-subInfo {
	color: grey;
}

.modal-body-info-2 {
	font-size: smaller;
}

@media screen and (max-width: 425px) {
	.modal-content {
		top: 350px;
		width: 95%;
	}
	.modal-button-1 {
		width: 80px;
		height: 20px;
		border-radius: 3px;
	}

	.modal-button-1-wrapper {
		font-size: x-small;
	}
}

.row{
  margin-left: 20px;
}

.row-poster-container {
	display: flex;
	overflow-x: auto;
	overflow-y: hidden;
	padding: 20px;
}

.row-poster-container::-webkit-scrollbar {
	display: none;
}

.row-poster {
	object-fit: contain;
	width: 100%;
	max-height: 100px;
	transition: 400ms;
	margin: 5px;
}

.row-poster:hover {
	-webkit-transform: scale(1.08);
	        transform: scale(1.08);
}

.row-poster-large {
	max-height: 250px;
}

.row-poster-large:hover {
	-webkit-transform: scale(1.09);
	        transform: scale(1.09);
}

.banner-container {
	color: white;
	object-fit: contain;
	height: 448px;
}

.banner-overlay{
	position: absolute;
	width: 100%;
	height: 448px;
	background-color: rgba(0, 0, 0, 0.25);
}

.banner-contents {
	margin-left: 30px;
	padding-top: 140px;
	height: 190px;
}

.banner-title {
	font-size: 3rem;
	font-weight: 800;
	padding-bottom: 0.3rem;
}

.banner-description {
	width: 45rem;
	line-height: 1.3;
	padding-top: 1rem;
	font-size: 0.8rem;
	max-width: 360px;
	height: 80px;
}

.banner-button {
	cursor: pointer;
	color: white;
	outline: none;
	border: none;
	font-weight: 700;
	border-radius: 0.2vw;
	padding-left: 2rem;
	padding-right: 2rem;
	margin-right: 1rem;
	padding-top: 0.5rem;
	background-color: rgba(51, 51, 51, 0.5);
	padding-bottom: 0.5rem;
}

.banner-button:hover {
	color: black;
	background-color: #e6e6e6;
	transition: 0.2s;
}

.banner-blur {
	height: 7.4rem;
	background-image: linear-gradient(
		180deg,
		transparent,
		rgba(37, 37, 37, 0.61),
		rgba(24, 24, 24)
	);
}

.navbar {
	position: fixed;
	top: 0;
	width: 100%;
	display: flex;
	justify-content: space-between;
	padding: 20px;
	height: 25px;
	z-index: 1;
	transition-timing-function: ease-in;
	transition: all 0.5s;
}

.navbar-show {
	background-color: rgb(24, 24, 24);
}

.navbar-netflix-logo {
	position: fixed;
	left: 20px;
	width: 80px;
	object-fit: contain;
}

.navbar-netflix-avatar {
	position: fixed;
	right: 20px;
	width: 30px;
	object-fit: contain;
}

